import React from 'react';
import ReactDOM from 'react-dom/client';
import './index.css';
import OverlordApp from './OverlordApp';
import ViewerApp from "./ViewerApp";

const root = ReactDOM.createRoot(document.getElementById('root'));

if(document.location.search === "?viewer") {
    root.render(
        <React.StrictMode>
            <ViewerApp/>
        </React.StrictMode>
    );
}else{
    root.render(
        <React.StrictMode>
            <OverlordApp/>
        </React.StrictMode>
    );
}